@import "../../constants/colors.scss";

.account-overview {
    color: black;
    width: auto;
    padding: 2em 2em;

    h1 {
        margin-bottom: 1em;
    }

    h3, span, p {
        font-weight: lighter;
    }

    h3 {
        margin-bottom: .75em;
    }

    span {
        color: rgb(162,162,162);
    }

    table {
        width: 100%;
        tr {
            padding: 1em 0;
            margin-bottom: 1em;
            display: flex;
            justify-content: space-between;
            border-bottom: solid 1px rgb(162,162,162);
        }
    }
    
}

@media (min-width: 800px) {
    .account-overview {
    }
}